import { Component, Input, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-masterdata/@core/permissions/permission.store';
import { DataStoreService } from './pics-masterdata/@core/service/data-store.service';
import { RBACINFO } from './pics-masterdata/@core/urls/rbac-url.config';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'masterdata',
  template: `
      <lib-masterdata></lib-masterdata>
  `,
  styles: [
  ]
})
export class MasterdataComponent implements OnInit, AfterViewInit {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public mastersEvent!: Observable<any>;
  @Input() public INPUTVALIDATIONMETHOD? : any;
  categoryForm: FormGroup;
  isTextOverflow = false;
  @ViewChild('nameInput') nameInput: ElementRef;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService,
    private fb: FormBuilder    
  ) {
    this.categoryForm = this.fb.group({
      name: [''],
    });    
  }
  ngOnInit() {
     this.mastersEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('INPUTVALIDATIONMETHOD', this.INPUTVALIDATIONMETHOD);
      this._storeservice.setData('HTTPSERVICE',val.HTTPSERVICE);
      this.categoryForm.get('name').valueChanges.subscribe(value => {
        this.checkTextOverflow(value);
      });      
    })    
  }

  ngAfterViewInit(): void {
    this.checkTextOverflow(this.categoryForm.get('name').value);
  }  

  onInput(event: Event, fieldName: string, fieldLabel: string, isRequired: boolean): void {
    this.checkTextOverflow((<HTMLInputElement>event.target).value);
  }

  checkTextOverflow(value: string): void {
    setTimeout(() => {
      const inputElement = this.nameInput.nativeElement as HTMLInputElement;
      if (inputElement) {
        this.isTextOverflow = inputElement.scrollWidth > inputElement.clientWidth;
      }
    });
  }
}

